<template>
  <div>
    <van-nav-bar title="发帖栏" right-text="发布" />
    <van-cell-group>
      <van-field v-model="value2" label="标题" placeholder="请输入内容" />
    </van-cell-group>
    <van-field
      readonly
      clickable
      name="picker"
      :value="value"
      label="版块"
      placeholder="点击选择发布的版块"
      @click="showPicker = true"
    />
    <van-popup v-model="showPicker" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns"
        @confirm="onConfirm"
        @cancel="showPicker = false"
      />
    </van-popup>
    <van-field
      v-model="message"
      rows="5"
      autosize
      type="textarea"
      maxlength="200"
      placeholder="这一刻的你在想什么..."
      show-word-limit
    />
    <van-field name="uploader">
      <template #input>
        <van-uploader v-model="uploader" :max-count="9" />
      </template>
    </van-field>
  </div>
</template>

<script>
export default {
  data() {
    return {
      uploader: [],
      value: "",
      value2: "",
      message: "",
      columns: [
        "信息工程学院",
        "旅游管理学院",
        "烹饪科技学院",
        "工艺美术学院",
        "经济管理学院",
        "国际商务学院",
      ],
      showPicker: false,
    };
  },
  methods: {
    onConfirm(value) {
      this.value = value;
      this.showPicker = false;
    },
  },
};
</script>

