import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
import home from '@/components/home.vue'
import mine from '@/components/mine.vue'
import sort from '@/components/sort.vue'
import search from '@/components/search.vue'
import ArticleDetail from '@/components/ArticleDetail'
import release from '@/components/release'
import login from '@/components/login'


const router = new VueRouter({
  routes: [
    { path: '/', redirect: '/home' },
    { path: '/home', component: home, meta: { showTab: true } },
    { path: '/mine', component: mine, meta: { showTab: true } },
    { path: '/sort', component: sort, meta: { showTab: true } },
    { path: '/search', component: search },
    { path: '/detail', component: ArticleDetail },//文章的详情页
    { path: '/release', component: release, meta: { showTab: true } },
    { path: '/login', component: login },
  ]
})
// 全局前置守卫
router.beforeEach(function (to, from, next) {
  //to表示将要访问的信息
  //from表示将要离开的信息对象
  //next()函数表示放行的意思

  if (to.path === '/mine' || to.path === '/release') {
    const token = localStorage.getItem('token')
    if (token) {
      next()
    } else {
      next('/login')
    }
  } else {
    next()
  }

})
export default router
