<template>
  <div>
    <template>
      <div>
        <van-cell>
          <!-- 标题区域的插槽 -->
          <template #title>
            <div class="title-box">
              <!-- 标题 -->
              <span>{{ title }}</span>
              <!-- 单张图片 -->
              <img
                v-if="image.length === 1"
                :src="image[0].img"
                class="thumb"
              />
            </div>
            <!-- 三张图片 -->
            <div class="thumb-box" v-if="image.length > 1">
              <img
                :src="item.img"
                class="thumb"
                v-for="item in image"
                :key="item.id"
              />
            </div>
          </template>
          <!-- label 区域的插槽 -->
          <template #label>
            <div class="label-box">
              <span>{{ user }}&nbsp;&nbsp;信息工程学院</span>
              <span>8945浏览</span>
              <span>{{ time }}</span>
            </div>
          </template>
        </van-cell>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      default: "",
      type: String,
    },
    user: {
      default: "",
      type: String,
    },
    time: {
      default: "",
      type: String,
    },
    image: {
      default() {
        return {};
      },
    },
  },
  methods: {},
};
</script>

<style lang="less" scoped>
.label-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.thumb {
  // 矩形黄金比例：0.618
  width: 113px;
  height: 70px;
  background-color: #f8f8f8;
  object-fit: cover;
}

.title-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.thumb-box {
  display: flex;
  justify-content: space-between;
}
</style>