<template>
  <div>
    <van-nav-bar
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
      :border="false"
    />
    <div class="text">登录页面</div>
    <div>测试账号:lele 测试密码:123456</div>
    <van-cell-group>
      <van-field
        v-model="username"
        left-icon="https://blele.oss-cn-beijing.aliyuncs.com/%E8%B4%A6%E5%8F%B7%E4%B8%AD%E5%BF%83%20%E8%B4%A6%E5%8F%B7%E7%AE%A1%E7%90%86.png"
        placeholder="请输入账号"
      />
      <van-field
        v-model="password"
        clearable
        left-icon="https://blele.oss-cn-beijing.aliyuncs.com/%E5%AF%86%E7%A0%81.png"
        placeholder="请输入密码"
        type="password"
      />
    </van-cell-group>
    <van-button type="primary" @click="login" block class="btn" round
      >登录</van-button
    >
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    login() {
      if (this.username === "lele" && this.password === "123456") {
        localStorage.setItem("token", "Bearer xxxx");
        this.$router.back();
      } else {
        localStorage.removeItem("token");
      }
    },
    onClickLeft() {
      this.$router.back();
    },
  },
};
</script>

<style lang="less" scoped>
.btn {
  width: 250px;
  margin: 0 auto;
  margin-top: 20px;
}
/deep/.van-icon__image {
  width: 24px;
  height: 24px;
}
.text {
  margin: 50px 0;
  text-align: center;
  font-size: 48px;
}
</style>