<template>
  <div>
    <van-pull-refresh
      v-model="isLoading"
      @refresh="onRefresh"
      success-text="刷新成功"
    >
      <van-nav-bar title="校园发布" />
      <van-swipe :autoplay="5000" indicator-color="white">
        <van-swipe-item v-for="image in images" :key="image.id">
          <img :src="image.img" class="img1" />
        </van-swipe-item>
      </van-swipe>
      <van-notice-bar
        scrollable
        text="学院填写请不要随便填写"
        mode="closeable"
      />
      <van-grid :column-num="4" :border="false">
        <van-grid-item
          v-for="item in lists"
          :key="item.id"
          :icon="item.icon"
          :text="item.name"
        />
      </van-grid>
      <!-- color="#1989fa"
        background="#ecf9ff" -->

      <van-tabs v-model="active" swipeable sticky animated>
        <van-tab v-for="item in tabs" :title="item.name" :key="item.id">
          <Article
            v-for="item in list"
            :key="item.id"
            :title="item.title"
            :user="item.user"
            :time="item.time"
            :image="item.image"
            @click.native="onClickCell(item.id)"
          ></Article>
        </van-tab>
      </van-tabs>
    </van-pull-refresh>
  </div>
</template>
<script>
import Article from "./Article.vue";
export default {
  name: "home",
  components: { Article },
  data() {
    return {
      active: 0,
      images: [],
      list: [],
      isLoading: false,
      tabs: [
        { id: 1, name: "热点" },
        { id: 2, name: "信息工程学院" },
        { id: 3, name: "旅游管理学院" },
        { id: 4, name: "烹饪科技学院" },
        { id: 5, name: "国际商贸学院" },
        { id: 6, name: "工艺美术学院" },
        { id: 7, name: "经济管理学院" },
      ],
      lists: [
        {
          id: 1,
          name: "奶茶",
          icon: "https://img66.foodjx.com/2/20200423/637232497928361812582.jpg",
        },
        {
          id: 3,
          name: "代买",
          icon: "https://img66.foodjx.com/2/20200423/637232497928361812582.jpg",
        },
        {
          id: 5,
          name: "二手商品",
          icon: "https://img66.foodjx.com/2/20200423/637232497928361812582.jpg",
        },
        {
          id: 6,
          name: "烘培蛋糕",
          icon: "https://img66.foodjx.com/2/20200423/637232497928361812582.jpg",
        },
      ],
    };
  },
  methods: {
    async getinfo() {
      const { data: res } = await this.$http.get("/school/index");
      this.images = res.data;
    },
    async ArticleList() {
      const { data: res } = await this.$http.get("/list");
      this.list = res.list;
    },
    onRefresh() {
      setTimeout(() => {
        location.reload();
        this.isLoading = false;
      }, 1000);
    },
    onClickCell(id) {
      this.$router.push({ path: "/detail", query: { id } });
    },
  },

  created() {
    this.getinfo();
    this.ArticleList();
  },
};
</script>
<style lang="less" scoped>
.img1 {
  width: 100%;
  height: 250px;
}
.van-nav-bar {
  background-color: rgb(116, 200, 99);
}
/deep/.van-nav-bar__title {
  color: white;
  font-size: 20px;
}
/deep/.van-icon__image {
  width: 100%;
  height: 100%;
}
/deep/.van-grid-item__text {
  color: black;
  font-weight: 600;
}
/deep/.van-tab--active {
  font-weight: 700;
}
</style>